.galleries {
  display: flex;
  flex-direction: row;
}

.leftBlock {
  display: flex;
  flex: 1;
  padding: 2px;
  flex-direction: column;
  align-items: flex-end;
}

.rightBlock {
  display: flex;
  flex: 1;
  padding: 2px;
  flex-direction: column;
}

.galleryLeftBlock {
  width: 275px;
  overflow: hidden;
}

.galleryRightBlock {
  width: 275px;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
}

.title {
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 0 16px;
}

.desc {
  font-size: 16px;
  padding: 0 16px;
  height: 50px;
  overflow-y: hidden;
}

.price {
  font-size: 24px;
  font-weight: 600;
  padding: 0 16px;
  margin: 0;
  padding: 0 16px;
}

.summary {
  font-size: 24px;
  font-weight: 600;
  color: red;
}

.button {
  width: 200px;
  height: 40px;
  background: green;
  color: white;
  border-radius: 8px;
  border: 0;
  cursor: pointer;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
