.root {
  position: relative;
}

.image {
  object-fit: cover;
}

.prevArrow,
.nextArrow,
.prevArrowVertical,
.nextArrowVertical {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: rgb(255, 255, 255, 0.7);
  position: absolute;
}

.prevArrow {
  left: 16px;
}

.nextArrow {
  right: 16px;
}

.prevArrowVertical {
  top: 16px;
}

.nextArrowVertical {
  bottom: 16px;
}
